* {
  font-family: Helvetica, Arial, sans-serif;
  color: #faf9f7;
}

body {
  padding: 60px;
  background: #27241d ;
}

input {
  width: 100%;
  height: 30px;
  font-size: 1.2 rem;
  color: #504a40;
  border: 2px solid #504a40;
  border-radius: 20px;
  padding: 0 10px;
}



.card-grid {
  display: flex;
  flex-direction: row;
  justify-content: center ;
  flex-wrap: wrap;
}

.card {
  /* align-content: center; */
  border: 2px solid #504a40;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  background: #423d33;
}

.card p {
  padding: 5px;
  text-align: center;
  font-weight: bold;
}

.card img {
  max-width: 100%;

}